/*--------------------------------------------------------------
# Inner Circle - Single Post

1. Override existing styles from parent theme SASS
2. File attachment
--------------------------------------------------------------*/

@import "../node_modules/@asu-design-system/design-tokens/build/scss/_variables"; 
@import "media-queries";

@mixin small-horiz-divider {
    content: '';
    height: 1px;
    width: 100%;
    max-width: $uds-size-spacing-32;
    display: block;
    position: absolute;
    top: 0;
    background-color: $uds-color-base-gray-3;
}

/*--------------------------------------------------------------
5. Hero + title layout
--------------------------------------------------------------*/

// Mobile first


// Desktop
// @include media-breakpoint-down(lg) {
    .single-hero {

        position:relative;

        .default-image {
            height:200px;
            background: url('../img/NetworkBlack.png') top center repeat;
        }

        img.wp-post-image {
            max-height:300px;
            object-fit: cover;
            width:100%;

            &.featured {
                max-height:515px;
            }
        }

        .category-wrap {
            background-color: #ffc627;
            text-align:right;
            height:3rem;

            .category-tags .btn {
                margin-right:1rem;
                margin-bottom:0;
                background-color: #ffc627;
                text-transform: uppercase;
            }
        }

        .title-wrap {

            background-color: #191919;
            color:#ffffff;
            padding: 1rem;
            display: inline-block;
            position:absolute;
            bottom:-3rem;
            left:0;
            border-top:2px solid #ffffff;
            border-right:2px solid #ffffff;
            max-width:66.6667%;

            h1 {
                /* Like an H2 */
                margin:0;
                font-size: 2.5rem;
                line-height: 2.75rem;
                letter-spacing: -.0875rem;
            }

            .entry-meta {
                margin-bottom: .5rem;
                margin-top: 1rem;
            }

            .tags-links {
                margin-bottom: 0;
                a {
                    color:#ffffff;
                }
            }

            svg {
                margin-right:$uds-size-spacing-1;
            }
        }
    }
// }

/*--------------------------------------------------------------
5. Page layout
--------------------------------------------------------------*/
.single {
    .container + .container {
        // Separation between hero area and body text.
        padding-top: $uds-size-spacing-12;
    }

    .row + .row {
        margin-top: $uds-size-spacing-6;
    }
}

/*--------------------------------------------------------------
2. File Attachment (.downloads)
--------------------------------------------------------------*/
.downloads {

    position: relative;
    margin: $uds-size-spacing-6 0;
    padding-top: $uds-size-spacing-6;

    &:before {
        @include small-horiz-divider;
    }

    img {
        margin-right: $uds-size-spacing-3;
    }

    img.attachment-icon {
        padding: $uds-size-spacing-3;
        border: 1px solid $uds-color-base-gray-2;
    }

    .media-body {

        p:first-child {
            font-weight: 700;
        }
    }
}

